body, div, h1, h2, h3, p, label, input, button, select, textarea, fieldset {
  display: block;
  margin: 0;
  border: 0;
  border-radius: 0;
  padding: 0;
}

ol, ul {
  margin: 0;
}

body, input, button, select, textarea, fieldset, h1, h2 {
  font: 10pt courier new;
}

input[type="text"], input[type="password"], button, select {
  height: 30px;
}

input[type="text"], input[type="password"], button, select, .fas, .lineHeight1 {line-height: 1;}

.fas {
  font-size: 14pt;
}

.absolute {position: absolute;}
.relative {position: relative;}

.hidden {visibility: hidden;}

.overflow-hidden {overflow: hidden;}

.grid {display: grid;}

.flex {display: flex;}

.flex-wrap {flex-wrap: wrap;}

.flex-col {flex-direction: column;}

.spaceAround   {justify-content: space-around;}
.spaceBetween  {justify-content: space-between;}
.justifyCenter {justify-content: center;}
.flexEnd       {justify-content: flex-end;}

.alignCenter {align-items: center;}

.bdrBox {
  box-sizing: border-box;
}

.m5  {margin: 5px;}

.mt10 {margin-top: 10px;}

.mr5  {margin-right:  5px;}
.mr10 {margin-right: 10px;}

.mb5  {margin-bottom:  5px;}
.mb10 {margin-bottom: 10px;}

.ml10 {margin-left: 10px;}

.mAuto {
  margin-right: auto;
  margin-left: auto;
}

.bdrBlack {border: 1px solid #000;}
.bdrGray  {border: 1px solid #999;}

.p5  {padding:  5px;}
.p10 {padding: 10px;}

.flex20 {width: 20%;}
.flex80 {width: 80%;}
.flex33 {width: 33.33%}
.flex50 {width: 50%}

.flex100, .wFull {width: 100%;}

.w100 {max-width: 100px;}
.w600 {max-width: 600px;}

.w-1\/2 {width: 50%;}

.bgWhite {background-color: #fff;}

.fs14 {font-size: 14pt;}
.fs16 {font-size: 16pt;}

.bold {font-weight: bold;}

.txtBlue                   {color: blue;}
.txtGray                   {color: gray;}
.txtRed                    {color: red;}
.txtBlack, .txtBlack:hover {color: black;}

.txtC {text-align: center;}
.txtR {text-align: right;}

.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.pointer {cursor: pointer;}

.underline-on-hover:hover {
  text-decoration: underline;
}

.clearSearchButton {
  top: 5px;
  right: 7px;
}

.list {
  grid-template-columns: min-content auto;
}

.searchFieldAny {
  padding: 5px 25px 5px 5px;
}

.suggestion:hover, .suggestion.active {
  background-color: gray;
  color: white;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .fas  {font-size: 24pt;}
  .fs14 {font-size: 14pt;}
  .fs16 {font-size: 16pt;}
}

@media only screen and (min-width: 601px) {
  .m-flex1 {flex-grow: 1;}
  .m-flex-row {flex-direction: row;}

  .m-mb0 {margin-bottom: 0;}

  .m-w100 {width: 100px;}
}
